:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --white-bg: hsl(0, 0%, 100%);
  --very-pale-blue-top-bg: hsl(225, 100%, 98%);
  --light-grayish-blue-card: hsl(227, 47%, 96%);
  --dark-grayish-blue-text: hsl(228, 12%, 44%);
  --very-dark-blue-text: hsl(230, 17%, 14%);
  --very-dark-blue-bg: hsl(230, 17%, 14%);
  --very-dark-blue-top-bg: hsl(232, 19%, 15%);
  --dark-desaturated-blue-card-bg: hsl(228, 28%, 20%);
  --dark-desaturated-blue-card-bg-hover: hsl(229, 28%, 27%);
  --desaturated-blue-text: hsl(228, 34%, 66%);
  --white-text: hsl(0, 0%, 100%);
  --toggle: hsl(230, 22%, 74%);
  --toggle-light-start: hsl(210, 78%, 56%);
  --toggle-light-end: hsl(146, 68%, 55%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:hover, a:visited, a:active, a:focus {
  text-decoration: none;
}

.container {
  width: 69.375rem;
  margin: 0 auto;
}

.card {
  background-color: var(--dark-desaturated-blue-card-bg);
  border-radius: 0.3125rem;
  padding: 1.5625rem;
  width: 15.9375rem;
}
.card:hover {
  background-color: var(--dark-desaturated-blue-card-bg-hover);
  cursor: pointer;
}

.section-totals {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 5rem;
}
.section-totals .card-big {
  text-align: center;
}
.section-totals .card-big.facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.section-totals .card-big.twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.section-totals .card-big.instagram {
  position: relative;
  overflow: hidden;
}
.section-totals .card-big.instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--instagram-end) 0%, var(--instagram-start) 98.02%);
}
.section-totals .card-big.youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.section-totals .card-big .card-big-top {
  display: inline-flex;
}
.section-totals .card-big .card-big-top img {
  align-self: center;
  margin-right: 0.3125rem;
}
.section-totals .card-big .card-big-top p {
  margin: 0;
  font-size: 0.75rem;
  align-self: center;
  line-height: 1;
}
.section-totals .card-big .card-big-middle {
  padding-top: 1.25rem;
}
.section-totals .card-big .card-big-middle .count-big {
  font-size: 2.875rem;
  font-weight: 700;
}
.section-totals .card-big .card-big-middle p {
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  font-size: 0.625rem;
}
.section-totals .card-big .card-big-bottom {
  display: inline-flex;
  padding-top: 1.25rem;
  font-size: 0.75rem;
}
.section-totals .card-big .card-big-bottom img {
  align-self: center;
  margin-right: 0.1875rem;
}
.section-totals .card-big .card-big-bottom.up {
  color: var(--limegreen);
}
.section-totals .card-big .card-big-bottom.up img {
  fill: var(--limegreen);
}

.section-daily {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.875rem;
}
.section-daily .card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 1.25rem;
  flex-wrap: wrap;
  height: 7.75rem;
}
.section-daily .card .views-percentage,
.section-daily .card .page-view {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.section-daily .card .views-percentage p,
.section-daily .card .page-view p {
  margin: 0;
}
.section-daily .card .page-view p {
  font-size: 2rem;
  font-weight: 700;
  line-height: 0.9;
}
.section-daily .card .views-percentage > img {
  width: 1.25rem;
  align-self: end;
}
.section-daily .card .views-percentage .views {
  display: flex;
  font-size: 0.75rem;
}
.section-daily .card .views-percentage .views.up {
  color: var(--limegreen);
}
.section-daily .card .views-percentage .views.down {
  color: var(--brightred);
}
.section-daily .card .views-percentage .views img {
  align-self: center;
}

.toggle-title {
  font-size: 0.8125rem;
  line-height: 100%;
  display: flex;
  align-self: center;
  padding-right: 7px;
  font-weight: 700;
}

.toggle-wrapper {
  position: relative;
  display: inline-block;
  width: 3.25rem;
  height: 1.5625rem;
}
.toggle-wrapper input {
  opacity: 0;
  width: 0;
  height: 0;
}
.toggle-wrapper .toggle {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 2.125rem;
  background: linear-gradient(225deg, var(--toggle-light-end) 0%, var(--toggle-light-start) 98.02%);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.toggle-wrapper .toggle:before {
  position: absolute;
  content: "";
  height: 1.25rem;
  width: 1.25rem;
  left: 0.1875rem;
  top: 0.15625rem;
  border-radius: 50%;
  background-color: var(--very-dark-blue-bg);
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.toggle-wrapper input:checked + .toggle:before {
  -webkit-transform: translateX(1.625rem);
  -ms-transform: translateX(1.625rem);
  transform: translateX(1.625rem);
}

input:checked + .toggle:before {
  background: var(--white-bg);
}

input:checked + .toggle {
  background: linear-gradient(225deg, var(--toggle) 0%, var(--toggle) 98.02%);
}

body {
  background-color: var(--very-dark-blue-bg);
  color: var(--white-text);
}

.section-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 3.75rem;
}

.section-heading__col-right {
  display: flex;
}

h1 {
  margin-bottom: 0;
}

.followers-count {
  font-size: 0.8125rem;
  color: var(--desaturated-blue-text);
}

#fem-smd {
  padding: 2.5rem 0;
}

.sub-title {
  margin-top: 3.75rem;
}
.sub-title h2 {
  margin-bottom: 0.625rem;
}